.form{
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: stretch;
  margin: 1rem;
}

.single.form{
  margin: 1rem 0px;
}

.textarea{
  display: block;
  width: 100%;
  font-size: 1rem;
  font-family: var(--type-first);
  resize: none;
  border: 1px solid #eee;
  padding: .5rem;
  border-radius: .2rem;
  background: #eee;
  transition: .2s;
}

.textarea:focus,
.textarea:hover{
  outline: none;
  border-color: #EA52F8;
  background: #fff;
  box-shadow: 0 0 0 3px #302B63;
}

.button{
  border: none;
  cursor: pointer;
  color: #333;
  background: transparent;
  font-size: 1rem;
  padding: 0 1rem;
  overflow: hidden;
}

.button:focus,
.button:hover{
  outline: none;
}

.button:focus svg path,
.button:hover svg path{
  fill: #302B63;
  stroke: #EA52F8;
}

.button:focus svg g,
.button:hover svg g{
  animation: latir .5s infinite;
}

@keyframes latir {

  from{
    opacity: 0;
  }

  to{
    opacity: 1;
  }

}