.photo{
  margin: auto;
  height: 36rem;
  border-radius: .2rem;
  background: #fff;
  display: grid;
  grid-template-columns: 36rem 20rem;
  grid-template-rows: auto 1fr auto;
  overflow: hidden;
  opacity: 0;
  transform: scale(.8);
  animation: scaleUp .3s forwards;
}

.single.photo{
  grid-template-columns: 1fr;
  height: auto;
}

@keyframes scaleUp {
  
  to{
    opacity: initial;
    transform: initial;
  }

}

.details{
  padding: 2rem 2rem 0 2rem;
}

.sigle .details{
  padding: 1rem 0px 0px 0px;
}

.img{
  grid-row: 1/4;
}

.single .img{
  grid-row: 1;
  border-radius: 0.4rem;
  overflow: hidden;
}

@media (max-width: 64rem){

  .photo{
    height: auto;
    max-height: calc(100vh - 4rem);
    overflow-y: auto;
    grid-template-columns: minmax(20rem, 40rem);
  }

  .img{
    grid-row: 1;
  }

}

.author{
  opacity: .5rem;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.author a:hover{
  text-decoration: underline;
}

.visualizacoes::before{
  content: '';
  display: inline-block;
  width: 16px;
  height: 10px;
  margin-right: .5rem;
  background: url('../Assets/Img/visualizacao-black.svg') no-repeat;
}

.attibutes{
  display: flex;
  font-size: 1.125rem;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.attibutes li{
  margin-right: 2rem;
}

.attibutes li::before{
  content: '';
  display: inline-block;
  height: 20px;
  margin-right: .5rem;
  position: relative;
  top: 3px;
  width: 2px;
  background: #555;
  margin-top: 5px;
}