@import url('https://fonts.googleapis.com/css2?family=Spectral:wght@700&display=swap');

* {
  box-sizing: border-box;
}

body{
  padding-top: 4rem;
  margin: 0px;
  color: #333;
  --type-first: Helvetica, Arial, sans-serif;
  --type-second: 'Spectral', georgia;
  font-family: var(--type-first);
}

/* width */
body::-webkit-scrollbar {
  width: 10px;
}

/* Track */
body::-webkit-scrollbar-track {
  background: #DDA0DD;
}

/* Handle */
body::-webkit-scrollbar-thumb {
  background: #0066FF;
  border-radius: 10px;
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background: #6E64E7;
}

a{
  text-decoration: none;
  color: #333;
}

h1,
h2,
h3,
h4,
p{
  margin: 0px;
}

ul,
li{
  list-style: none;
  padding: 0px;
  margin: 0px;
}

img{
  display: block;
  max-width: 100%;
}

button, input{
  display: block;
  font-size: 1rem;
  font-size: var(--type-first);
  color: #333;
}

.container {
  max-width: 50rem;
  padding: 0 1rem;
  margin: 0 auto;
}

.mainContainer{
  margin-top: 2rem;
}

.title{
  font-family: var(--type-second);
  line-height: 1;
  font-size: 3rem;
  margin: 1rem 0;
  position: relative;
  z-index: 1;
}

.title::after{
  content: '';
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  background: linear-gradient(#EA52F8, #0066FF);
  position: absolute;
  bottom: 5px;
  left: -5px;
  border-radius: .2rem;
  z-index: -1;
}

.animeLeft{
  opacity: 0;
  transform: translateX(-20px);
  animation: animeLeft .3s forwards;
}

@keyframes animeLeft {
    to{
      opacity: 1;
      transform: initial;
    }
}

.App{
  display: flex;
  flex-direction: column;
  min-height: calc(100vh + 10rem);
}

.AppBody{
  flex: 1;
}

.VictoryContainer{
  height: initial !important;
}